.section-description {
  padding-top: size(96px);
  padding-bottom: size(96px);

  @include vp-1439 {
    padding-top: 96px;
    padding-bottom: 96px;
  }

  @include vp-1023 {
    padding-top: 64px;
    padding-bottom: 24px;
  }

  .container {
    display: flex;

    @include vp-1023 {
      flex-direction: column;
      padding-right: 0;
      padding-left: 0;
    }
  }

  &__title-wrapper {
    flex-shrink: 0;
    width: 25%;
    padding: size(6px) size(48px) 0 0;

    @include vp-1439 {
      padding: 6px 48px 0 0;
    }

    @include vp-1279 {
      width: 20%;
    }

    @include vp-1023 {
      position: relative;
      margin-bottom: 0;
      padding-top: 0;
      padding-right: 24px;
      padding-left: 24px;
      padding-bottom: 24px;
      width: 100%;

      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;

        width: 100%;
        height: 1px;

        background-color: $color-nero-15;
      }
    }

    @include vp-767 {
      width: 100%;
      padding-right: 16px;
      padding-left: 16px;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 0;
  }

  &__title--desc {
    @include vp-1023 {
      display: none;
    }
  }

  &__title--mob {
    display: none;

    @include vp-1023 {
      display: block;
    }
  }

  &__text-wrapper {
    column-count: 2;
    column-gap: size(112px);

    @include vp-1439 {
      column-gap: 112px;
    }

    @include vp-1279 {
      column-gap: 60px;
    }

    @include vp-1023 {
      position: relative;

      padding-top: 24px;
      padding-right: 24px;
      padding-left: 24px;
      padding-bottom: 16px;

      column-count: inherit;
      column-gap: 0;

      transition: background-color $trans-default;

      &.is-active {
        background-color: $color-spring-wood;
      }

      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;

        width: 100%;
        height: 1px;

        background-color: $color-nero-15;
      }
    }

    @include vp-767 {
      padding-right: 16px;
      padding-left: 16px;
    }

    p {
      margin-top: 0;
      margin-bottom: 20px;
      page-break-inside: avoid;

      visibility: visible;
      opacity: 1;
      max-height: 100%;

      transition: opacity $trans-default;

      &.is-hidden {
        margin-bottom: 0;
        visibility: hidden;
        max-height: 0;
        opacity: 0;
      }
    }
  }

  &__button-text {
    display: none;

    -webkit-tap-highlight-color: transparent;

    @include vp-1023 {
      display: flex;
      align-items: center;

      margin-top: 64px;

      padding-top: 8px;
      padding-right: 0;
      padding-bottom: 8px;
      padding-left: 0;

      background-color: $color-transparent;
      border: none;

      cursor: pointer;

      &.is-hidden {
        display: none;
      }

      svg {
        flex-shrink: 0;
        margin-right: 8px;
      }
    }
  }
}
